/*滚动条整体部分,必须要设置*/
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: #333;
}
/*滚动条的轨道*/
::-webkit-scrollbar-track {
  background-color: #FFFFFF;
}
/*滚动条的滑块按钮*/
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #cccccc;
}
/*滚动条的上下两端的按钮*/
::-webkit-scrollbar-button {
  height: 0px;
  background-color: #FFFFFF;
}

