.bh-index .con-top {
  line-height: 40px;
}
.bh-index .gridbox {
  display: grid;
  grid-template-columns: 100px 100px 100px;
  /* 3个值代表3列  值决定列有多宽 */
  grid-template-rows: 50px 50px 50px;
  /* 2个值代表2行  值决定行有多高 */
}
.bh-index .gridbox div:nth-child(2n) {
    background-color: #ffa790;
}
.bh-index .gridbox div:nth-child(2n+1) {
    background-color: #fabec0;
}
.bh-index .gridbox .item1 {
    /* 独立占据整行 */
    grid-column: 1 / 4;
    /* 缩写 */
    grid-column-start: 1;
    /* 从第一条网格线开始 */
    grid-column-end: 4;
    /* 到第四条网格线结束 */
}

